import {UsersAPI} from '../API/api';
import {updateObjectInArray} from '../utils/object-helpers';

const FOLLOW = "FOLLOW";
const UNFOLLOW = "UNFOLLOW";
const SET_USERS = "SET-USERS";
const SET_PAGE = "SET-PAGE";
const SET_USERS_COUNT = "SET-USERS-COUNT";
const TOGGLE_IS_FETCHING = "TOGGLE-IS-FETCHING";
const TOGGLE_FOLLOWING_PROGRESS = "TOGGLE-FOLLOWING-PROGRESS";

let initialState = {
    users:[],
    pageSize: 100,
    totalUsersCount: 0,
    currentPage: 1,
    isFetching: true,
    followingInProgress: [2,3],
    fake: 10
}

export const usersReducer = (state = initialState, action) => {
    switch(action.type){ 
        case "FAKE":
            return {
                ...state, 
                fake: state.fake +1
            }
        case FOLLOW:
            return {
                ...state,
                users: updateObjectInArray(state.users, action.userID, "id", {followed: true})
            };
        case UNFOLLOW:
            return {
                ...state,
                users: updateObjectInArray(state.users, action.userID, "id", {followed: false})
            };
        case SET_USERS:
            return{
                ...state, users:action.users
            } 
        case SET_PAGE:
            return{
                    ...state, currentPage: action.currentPage,
            } 
        case SET_USERS_COUNT:
            return{
                    ...state, totalUsersCount: action.totalCount
            }
        case TOGGLE_IS_FETCHING:
            return{
                ...state, isFetching: action.isFetching
            }
        case TOGGLE_FOLLOWING_PROGRESS:
            return{
                ...state, 
                followingInProgress: action.isFetching 
                ? [...state.followingInProgress, action.userId]
                : state.followingInProgress.filter(id => id != action.userId)
            }                  
        default :
            return state;    
    }
}


export const follow = (userID) => ({type: FOLLOW, userID})
export const unfollow = (userID) => ({type: UNFOLLOW, userID});
export const setUsers = (users) => ({type: SET_USERS, users});
export const setCurrentPage = (currentPage) => ({type: SET_PAGE, currentPage});
export const setUsersTotalCount = (totalCount) => ({type: SET_USERS_COUNT, totalCount});
export const toggleIsFetching = (isFetching) => ({type: TOGGLE_IS_FETCHING, isFetching })
export const toggleFollowingProgress = (isFetching, userId) => ({type: TOGGLE_FOLLOWING_PROGRESS, isFetching, userId });

export const getUsersThunkCreator = (currentPage, pageSize) => async (dispatch) => {
    dispatch(toggleIsFetching(true));
    let data = await UsersAPI.getUsers(currentPage,pageSize)
                dispatch(toggleIsFetching(false));    
                dispatch(setUsers(data.items));
                dispatch(setUsersTotalCount(data.totalCount));
                dispatch(setCurrentPage(currentPage))
            }

const followUnfollowFlow = async (dispatch, userId, apiMethod, actionCreator) => {
    dispatch(toggleFollowingProgress(true, userId));
    let data = await apiMethod(userId);
        if(data.resultCode === 0){
            dispatch(actionCreator(userId))
        }
        dispatch(toggleFollowingProgress(false, userId))
}            


export const followThunkCreator = (userId) => async (dispatch) => {
        followUnfollowFlow(dispatch, userId,  UsersAPI.follow.bind(UsersAPI), follow)
        }



export const unfollowThunkCreator = (userId) => async (dispatch) => {
        followUnfollowFlow(dispatch, userId, UsersAPI.unfollow.bind(UsersAPI), unfollow)
    }




export default usersReducer;