const ADD_MESSAGE = "ADD-MESSAGE";

let initialState = {
    messages:[
        {id:1, message:"Loorem lorem lorem lorem"},
        {id:2, message:"Hi"},
        {id:3, message:"Yo"},
        {id:4, message:"Fuck u"},
        {id:5, message:"Shhhhit"},
        {id:6, message:"mmm react is so cool mm"}
    ],
    dialogs:[
        {id: 1, name: 'Roma Vinograd', message:'im cooking pasta', date:'today', time:'12:30AM'},
        {id: 2, name: "Nastya Nastya", message:"let's cook chocolate", date:"20.03", time:"12:30AM"},
        {id: 3, name: "Ilya Ilya", message:"Ya Ilya barista kofeini coffee mesto", date:"yesturday", time:"12:30AM"},
        {id: 4, name: "Mom", message:"whaere are you tvou mat'", date:"today", time:"12:30AM"},
        {id: 5, name: "Roma Vinograd", message:"i'm cooking pasta", date:"today", time:"12:30AM"},
        {id: 6, name: "JhJjjh", message:"i'm cooking pasta", date:"today", time:"12:30AM"}
    ]
}

export const dialogsReducer = (state = initialState, action) => {
    switch(action.type){
        case ADD_MESSAGE: 
            let text = action.message;
            return {
                ...state,
                messages:[...state.messages, {id: 7, message: text}]
            };
        default :
            return state;    
    }
}


export const addMessageActionCreator = (message) => ({type: ADD_MESSAGE, message})

export default dialogsReducer;