import React from "react";
import s from "./Users.module.css";
import { Row, Col, Container } from 'react-bootstrap';

import Paginator from '../common/Paginator/Paginator';
import User from './User';

let Users = ({currentPage, totalUsersCount, pageSize, onPageChange, users, ...props}) => {

    return <div>
        <h3 className={s.title}>Find profiles</h3>
        <Paginator currentPage={currentPage} totalItemsCount={totalUsersCount} pageSize={pageSize} onPageChange={onPageChange}/>
        <Container fluid>
        <Row>
        {users.map(u => <User key={u.id} 
                               user={u}
                               followingInProgress={props.followingInProgress}
                               followThunkCreator={props.followThunkCreator}
                               unfollowThunkCreator={props.unfollowThunkCreator} />)}
        </Row>
        </Container>
    </div>
}

export default Users;