import React from "react";
import s from "./Users.module.css";
import { Row, Col, Container } from 'react-bootstrap';
import userPhoto from "../../assets/images/user.png";
import {NavLink} from "react-router-dom";

let User = ({user, followingInProgress, unfollowThunkCreator, followThunkCreator}) => {
    return (<Col lg={3} sm={12} key={user.id}>
                <div className={s.userBox}>
                    <NavLink to={'/profile/' + user.id}>
                    <img src={ user.photos.small !=null ? user.photos.small : userPhoto } alt=""/>
                    <h4 className={s.userName}>{user.name}</h4>
                    <p className={s.location}>{"u.location.country"}, {"u.location.city"}</p>
                    <p className={s.status}>{user.status}</p>
                    </NavLink>
                    {user.followed 
                                ? <button disabled={followingInProgress.some(id => id === user.id)} onClick={() => {
                                   unfollowThunkCreator(user.id)
                                    }}>Unfollow</button> 
                                : <button disabled={followingInProgress.some(id => id === user.id)} onClick={() => {
                                    followThunkCreator(user.id)
                                    }}>Follow</button>}
                </div>
        </Col>)}

export default User;