const ADD_EVENT = "ADD-EVENT";
const UPDATE_NEW_EVENT_TEXT = "UPDATE-NEW-EVENT-TEXT";

let initialState = {
            eventsName:[
                {id:1, eventName: "Event 1"},
                {id:2, eventName: "Event 2"}
            ],
            newEventText: "hghghgh"
        }

export const listingReducer = (state = initialState, action) => {
    switch(action.type){
        case ADD_EVENT:
            let newEvent = state.newEventText

            return {
                ...state,
                eventsName: [...state.eventsName, {id:3, eventName: newEvent}],
                newEventText: ""
            }
        case UPDATE_NEW_EVENT_TEXT:
            return {
                ...state,
                newEventText: action.newEventText
            }
        default :
            return state;    
    }
}


export const addEventActionCreator = () => ({type: ADD_EVENT})
export const updateNewEventTextActionCreator = (text) => ({type: UPDATE_NEW_EVENT_TEXT, newEventText: text});

export default listingReducer;