import {UsersAPI, profileAPI} from '../API/api';

const ADD_POST = "ADD-POST";
const SET_USER_PROFILE = "SET-USER-PROFILE";
const SET_STATUS = "SET-STATUS";
const DELETE_POST = "DELETE-POST"

let initialState = {
    posts: [
        {id:1, postText:"hi i have eaten my dog", likes:2 }
        // {id:2, postText:"oops im so antisocial", likes:0 },
        // {id:3, postText:"just wanna drink some wine and listen to asap rocky", likes:1 }
    ],
    profile: null,
    status: ''
}

export const profileReducer = (state = initialState, action) => {
    switch(action.type){
        case ADD_POST:{
            let text = action.post;
            return {
                ...state,
                posts: [{id: 4, postText: text, likes: 0}, ...state.posts]
                };
        }    
        case SET_USER_PROFILE:{
            return {...state, profile: action.profile}
        }
        case SET_STATUS: {
            return {...state, status: action.status}
        }
        case DELETE_POST: {
            return {...state, posts: state.posts.filter(p => p.id != action.postId)}
        }   
        default:
            return state;    
        }  
}

export const addPostActionCreator = (post) => ({type: ADD_POST, post});
export const setUserProfile = (profile) => ({type: SET_USER_PROFILE, profile});
export const setStatus = (status) => ({type: SET_STATUS, status});
export const deletePost = (postId) => ({type: DELETE_POST, postId})


export const getUserProfileThunkCreator = (userId) => async (dispatch) => {
    let response = await UsersAPI.getUserProfile(userId);   
            dispatch(setUserProfile(response.data));
        }

export const getStatusThunkCreator = (userId) => async (dispatch) => {
    let response = await profileAPI.getStatus(userId)  
            dispatch(setStatus(response.data));
        }

export const updateStatusThunkCreator = (status) => async (dispatch) => {
    let response = await profileAPI.updateStatus(status)   
            if(response.data.resultCode === 0){
                dispatch(setStatus(status));
            }
        }


export default profileReducer;