import React from 'react';
import { setCurrentPage, followThunkCreator,
    unfollowThunkCreator } from '../../redux/usersReducer';
import { connect } from "react-redux";
import Users from './Users.jsx';
import Preloader from '../common/preloader/Preloader';
import { getUsersThunkCreator } from '../../../src/redux/usersReducer';
import { compose } from 'redux';
import { getUsers, getPageSize, getTotalUsersCount, getCurrentPage, getIsFetching, getFollowingInProgress } from '../../../src/redux/usersSelectors';

class UsersContainer extends React.Component{


    componentDidMount(){
        let {currentPage, pageSize} = this.props;
        this.props.getUsers(currentPage, pageSize);
    }

    onPageChange = (pageNumber) =>{
        let {pageSize} = this.props;
        this.props.getUsers(pageNumber, pageSize)
    }

    render(){
        console.log("RENDER")
        return <>
        {this.props.isFetching ? <Preloader /> : null}
        <Users totalUsersCount={this.props.totalUsersCount}
                pageSize={this.props.pageSize} 
                users={this.props.users}
                currentPage={this.props.currentPage}
                onPageChange={this.onPageChange}
                followThunkCreator={this.props.followThunkCreator}
                unfollowThunkCreator={this.props.unfollowThunkCreator}
                isFetching={this.props.isFetching}
                followingInProgress={this.props.followingInProgress}/>
        </>
    }}


let mapStateToProps = (state) => {
    console.log("MSTP");
    return {
        users: getUsers(state),
        pageSize: getPageSize(state),
        totalUsersCount: getTotalUsersCount(state),
        currentPage: getCurrentPage(state),
        isFetching: getIsFetching(state),
        followingInProgress: getFollowingInProgress(state)
    }
}

export default compose(
    connect(mapStateToProps, {setCurrentPage, getUsers: getUsersThunkCreator, followThunkCreator, unfollowThunkCreator}
 ) (UsersContainer)); 
